export default {
    data() {
        return {
            current: {
                title: "",
                artist: ""
            },
            song: true,
            isplaying: false,
            allMusic: null,
            index: 0,
			player: "",
			
        };
    },
    methods: {
        async initPlayer() {
            if (this.allMusic !== []) {
                this.current = await this.allMusic[this.index];
                this.player.src = `http://localhost:4000/${this.current.music.path}`;
            } else {
                this.song = true;
            }
        },
        async getAllSongs() {
            try {
                let response = await this.$axios.$get("/music");
                if (response === []) {
                    this.song = true;
                    this.current = null;
                } else {
                    this.song = false;
                    this.allMusic = response;
                }
                await this.initPlayer();
            } catch (err) {
                this.current = null;
                console.log(err);
            }
		},
		play(song) {
			if (song) {
				this.current = song
				this.player.src = `http://localhost:4000/${this.current.music.path}`
			}
			this.player.play()
			this.isplaying = true
		},
		pause() {
			this.player.pause();
			this.isplaying = false;
		},
		next() {
			this.index++
			if (this.index > this.allMusic.length - 1) {
				this.index = 0
			}
			this.current = this.allMusic[this.index]
			this.play(this.current)
		},
		prev() {
			this.index--
			if (this.index < 0) {
				this.index = this.allMusic.length - 1
			}
			this.current = this.allMusic[this.index]
			this.play(this.current)
		},

    },
    created() {
        if (process.client) {
            this.player = new Audio();
        }
        this.getAllSongs();
    }
};
